.slideshow {
  box-sizing: border-box;
  margin: 0 auto; }
  .slideshow .slideshow-container,
  .slideshow .dot-container {
    position: relative;
    margin: auto; }
  .slideshow .mySlides {
    display: none; }
  .slideshow .prev,
