    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .slideshow .prev {
    left: -4rem; }
  .slideshow .next {
    right: -3rem; }
  .slideshow .prev:hover,
  .slideshow .next:hover {
    -webkit-animation: highlight 8s infinite;
            animation: highlight 8s infinite; }
  .slideshow .text {
